import React from "react";
import { Props } from "./Props";
import { State } from "./State";
import { TestDisplay } from "./TestDisplay/TestDisplay";
import { connect } from "react-redux";
import { AddTest } from "../../Redux/GameDataReducer/Level/Actions";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { GetActivePlayer } from "../../Helper/StoreHelper";
import { LevelState } from "../../Redux/GameDataReducer/PlayerReducer/LevelData";

class LevelDisplayInternal extends React.Component<Props, State> {

    public render() {
        const tests = this.props.Level.tests.map((t, index) => <TestDisplay Test={t} key={index} />);
        
        return (
            <>
                <div>
                    <b>{this.props.Level.name}</b>
                    <br />
                    <i>{this.props.Level.description}</i>
                </div>
                {tests}
                {this.props.Level.id === "12" && <><button onClick={this.props.AddTest}>Add custom test</button><br/></>}
                {this.props.Status === LevelState.Completed && <b style={{color: "green"}}>Level completed!</b>}
                {this.props.Status === LevelState.NonValidMachineUsed && <b style={{color: "red"}}>Machine used that is not allowed this level</b>}

            </>
        );
    }
}

const mapStateToProps = (state: GameData) => {
    return {
        Level: state.activeLevel!,
        Status: GetActivePlayer(state)!.levelData.find((l) => l.Id === state.activeLevel!.id)!.State
    };
};

export const LevelDisplay = connect(
    mapStateToProps,
    { AddTest }
)(LevelDisplayInternal);